<template>
  <el-dialog title="选择机器" :visible.sync="dialogVisible" width="85%">
    <el-divider></el-divider>
    <el-form inline ref="queryForm" :model="queryForm" label-width="120px">
      <el-form-item label="全选:" class="choose_machine">
        <el-checkbox v-model="isChooseAll" @change="selectAll" style="margin-right: 20px"></el-checkbox>
      </el-form-item>
      <!--<el-form-item label="取消所有选择:">-->
      <!--<el-checkbox v-model="isChooseAllNot"  @change="selectAllNot" style="margin-right: 20px"></el-checkbox>-->
      <!--</el-form-item>-->
      <el-form-item label="MAC地址" prop="deviceMac">
        <el-input v-model="queryForm.deviceMac" placeholder="请输入MAC地址" clearable></el-input>
      </el-form-item>
      <!-- <el-form-item label="省" prop="province ">
        <el-input v-model="queryForm.province" placeholder="请输入省份" clearable></el-input>
      </el-form-item>
      <el-form-item label="市" prop="city">
        <el-input v-model="queryForm.city" placeholder="请输入城市" clearable></el-input>
      </el-form-item> -->

      <el-form-item label="省份" prop="province">
        <el-select v-model="queryForm.province" placeholder="请选择省区" @change="changeProvince(queryForm.province)" style="width: 100%" filterable remote clearable reserve-keyword>
          <el-option v-for="province in provinces" :key="province.id" :label="province.name" :value="province.code"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="城市" prop="city">
        <el-select :disabled="!queryForm.province" v-model="queryForm.city" placeholder="请选择市区" @change="changeCity(queryForm.city)" style="width: 100%" filterable remote clearable reserve-keyword>
          <el-option v-for="city in cities" :key="city.id" :label="city.name" :value="city.code"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称" prop="name">
        <el-input v-model="queryForm.name" placeholder="请输入名称" clearable></el-input>
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input v-model="queryForm.address" placeholder="请输入地址" clearable></el-input>
      </el-form-item>
      <el-form-item label="产品型号" prop="productId">
        <el-autocomplete class="inline-input" v-model="productName" :fetch-suggestions="querySearch" placeholder="请输入选择型号" @select="handleSelect" clearable></el-autocomplete>
      </el-form-item>
      <el-form-item label=" ">
        <el-button type="primary" @click="onSubmit">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" row-key="id">
        <el-table-column type="selection" width="55" reserve-selection> </el-table-column>
        <el-table-column label="MAC地址" prop="deviceMac"></el-table-column>
        <el-table-column label="名称" prop="name">
          <template slot-scope="scope">
            {{ scope.row.name ? scope.row.name : '无' }}
          </template>
        </el-table-column>
        <el-table-column label="产品型号" prop="productName">
          <template slot-scope="scope">
            {{ scope.row.productName ? scope.row.productName : '无' }}
          </template>
        </el-table-column>
        <el-table-column prop="deviceAddress" label="地址">
          <template slot-scope="scope">
            {{ scope.row.deviceAddress ? scope.row.deviceAddress : '无' }}
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
        <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
      </div>
    </div>

    <!-- 已选择  -->
    <div v-if="multipleSelection.length">
      <el-tag v-for="(tag, idx) in multipleSelection" :key="idx" closable :disable-transitions="true" @close="handleClose(tag.id)" style="margin:5px">
        {{ tag.deviceMac }}
      </el-tag>
    </div>

    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,

      queryForm: {
        deviceMac: '',
        address: '',
        name: '',
        productId: '',
        province: '',
        city: '',
        size: 10,
        page: 1,
        total: ''
      },
      deviceCategoryName: '',
      tableData: [],
      multipleSelection: [],
      chooseStr: '',
      chooseNameStr: '',

      // 选择所有
      isChooseAll: false,
      isChooseAllNot: false,
      selectArr: [],
      onshow: true,
      productName: '',
      //省份城市
      cityData: [],
      provinces: [],
      cities: [],
      hideUploadEdit: false,
      arrive: '', //具体信息
      provinceName: '',
      cityName: ''
    }
  },
  methods: {
    handleSelect(item) {
      console.log(item)
      this.productName = item.name
      this.queryForm.productId = item.id
    },
    // 输入联想
    querySearch(queryString, cb) {
      this.associateQuery(queryString, cb, '/product/query', 'httpGet')
    },
    //联想查询
    associateQuery(queryString, cb, url, method) {
      var params = { size: 10, page: 1, total: '', state: 1, name: queryString }
      this[method](url, params, null, true).then(res => {
        res.rows.map(i => (i.value = i.name))
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },
    selected(item) {
      this.isChooseAll = false
      this.isChooseAllNot = false
      this.dialogVisible = true
      if (item) {
        this.selectArr = item.split(',')
      } else {
        this.selectArr = []
      }
    },
    // 确定
    confirm() {
      // console.log(this.chooseNameStr)
      this.$emit('getName', this.chooseStr, this.chooseNameStr)
      this.dialogVisible = false
    },

    // 选择所有机器
    selectAll() {
      // 选中所有门店
      if (this.isChooseAll) {
        let params = Object.assign({}, this.queryForm)
        // params.regionIds = this.getQueryRegion();
        params.size = 999999
        this.httpGet('/device/query_all', params).then(ret => {
          this.handleSelectionChange(ret.rows)
        })
      } else {
        this.multipleSelection = []
      }
    },
    // tag取消
    handleClose(id) {
      // debugger
      this.multipleSelection = this.multipleSelection.filter(s => s.id !== id)
      const cancel = this.tableData.filter(g => g.id === id)
      if (cancel && cancel.length > 0) this.$refs.multipleTable.toggleRowSelection(cancel[0])
    },

    // 多选改变
    handleSelectionChange(val) {
      this.multipleSelection = val
      this.chooseStr = ''
      this.chooseNameStr = ''
      let arr = []
      let nameArr = []
      val.map(item => {
        arr.push(item.id)
        nameArr.push(item.name ? item.name : item.deviceMac)
      })
      this.chooseStr = arr.join(',')
      this.chooseNameStr = nameArr.join(',')
    },

    onSubmit() {
      this.queryForm.page = 1
      this.onshow = false
      this.loadData()
    },

    changeDate(e) {
      this.queryForm.StartDate = this.$moment(e[0]).format('YYYY-MM-DD')
      this.queryForm.EndDate = this.$moment(e[1]).format('YYYY-MM-DD')
    },

    loadData() {
      this.queryForm.total = ''
      if (this.onshow) {
        this.httpPost('/device/queryByIds', this.selectArr).then(res1 => {
          if (res1 == null) {
            this.onSubmit()
            return
          }
          this.$refs.multipleTable.clearSelection()
          this.selectArr.map(item => {
            let sub = res1.filter(i => i.id == item)
            for (var i = 0; i < sub.length; i++) {
              sub[i] && this.$refs.multipleTable.toggleRowSelection(sub[i])
            }
          })
          this.onSubmit()
        })
      } else {
        let params = Object.assign({}, this.queryForm)
        this.httpGet('/device/query_all', params).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      }
    },
    // 省份城市
    async getDictionary(parentCode, type) {
      const params = { parentCode: parentCode }
      let ret = await this.httpGet('/dict/query_by_code', params, null, true)
      return ret
    },
    // 省份切换
    async changeProvince(province) {
      // this.provinceName
      let res = await this.getDictionary(province, 2)
      console.log(res)
      this.cities = res
      // this.getCityName(province, 2, this.provinces)
    },
    // 城市切换
    async changeCity(city) {
      // let res = await this.getDictionary(city, 3)
      // console.log(res)
      // this.counties = res
      // this.getCityName(city, 3, this.cities)
    },
    // getCityName(code, type, arr) {
    //   const index = arr.findIndex(item => item.code === code)
    //   // console.log(arr[index].name)
    //   if (type === 2) this.provinceName = arr[index].name
    //   if (type === 3) this.cityName = arr[index].name
    //   // if (type === 1) this.countryName = arr[index].name
    // },
  },
  watch: {
    dialogVisible(n, o) {
      if (n === true) {
        this.queryForm.deviceMac = ''
        this.loadData()
      }
    }
  },

  async mounted() {
    let res = await this.getDictionary('system.address', 1)
    this.provinces = res
  }
}
</script>

<style lang="scss" scoped>
.el-dialog__body {
  padding: 10px 20px;
}

/deep/.el-form {
  .el-form-item__content {
    & > div {
      width: 240px !important;
    }
  }
  .choose_machine {
    & > div {
      width: 240px !important;
    }
  }
}
</style>
